export default function PrimarySearchAppBar() {
  const classes = useStyles();
  const [navIndex, setNavIndex] = useState(0);
  const [state, setState] = React.useState({
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}>
      <List>
        {[
          { name: 'Explore', content: 'about' },
          { name: 'My items', content: 'how-it-works' },
          { name: 'Following', content: 'pbt-token' },
          { name: 'Activity', content: 'roadmap' },
        ].map((tab, index) => (
          <ListItem button onClick={toggleDrawer(anchor, false)} key={index}>
            <ListItemText primary={tab.name} className={classes.menuTitle} />
          </ListItem>
        ))}
      </List>
      <Divider />
      <List>
        {[
          { name: 'How it works', id: 'https://farm.polkabridge.org/' },
          { name: 'Community', id: 'https://launchpad.polkabridge.org/' },
        ].map((tab, index) => (
          <a href={tab.id} className={classes.mobileLink}>
            <ListItem button key={tab.name}>
              <ListItemText primary={tab.name} className={classes.menuTitle} />
            </ListItem>
          </a>
        ))}
        <ListItem button>
          <a href="docs/whitepaper.pdf" style={{ textDecoration: 'none' }}>
            <ListItemText primary={'Read Whitepaper'} className={classes.menuTitlePink} />
          </a>
        </ListItem>
      </List>
    </div>
  );

  return (
    <div className={classes.grow}>
      <AppBar position="sticky" style={{ background: 'white', boxShadow: 'none', borderBottom: '1px solid #e9e9e9' }}>
        <Toolbar className="d-flex justify-content-around">
          {' '}
          <Link to="/">
            <Typography className={classes.title} variant="h6" noWrap>
              <img src="images/symbol.png" alt="logo" height="40px" /> MarketPlace
            </Typography>
          </Link>
          <div className="d-flex justify-content-end">
            <Link to="/">
              <Typography
                variant="subtitle1"
                noWrap
                className={navIndex === 0 ? classes.tabsActive : classes.tabs}
                onClick={() => setNavIndex(0)}>
                Explore
              </Typography>
            </Link>
            <Link to="/profile">
              <Typography
                variant="subtitle1"
                noWrap
                className={navIndex === 1 ? classes.tabsActive : classes.tabs}
                onClick={() => setNavIndex(1)}>
                My items
              </Typography>
            </Link>
            <Typography className={classes.tabs} variant="subtitle1" noWrap>
              Following
            </Typography>
            <Link to="/profile">
              {' '}
              <Typography
                variant="subtitle1"
                noWrap
                className={navIndex === 3 ? classes.tabsActive : classes.tabs}
                onClick={() => setNavIndex(3)}>
                Activity
              </Typography>{' '}
            </Link>
            <Typography className={classes.tabs} variant="subtitle1" noWrap>
              |
            </Typography>
            <Typography className={classes.tabs} variant="subtitle1" noWrap>
              How it works
            </Typography>
            <Typography className={classes.tabs} variant="subtitle1" noWrap>
              Community
            </Typography>
            <div className={classes.sectionDesktop}>
              <div>
                <Button className={classes.balanceButton}>
                  <div className={classes.buttonIcon}>
                    <AccountBalanceWallet className={classes.icon} />
                  </div>
                  <div>
                    <strong style={{ color: '#616161' }}>38</strong>
                  </div>
                </Button>
              </div>
            </div>
          </div>
          <div className={classes.sectionMobile}>
            <div>
              <img src="logo.png" alt="logo" height="50px" />
            </div>

            <div>
              {['right'].map((anchor) => (
                <React.Fragment key={anchor}>
                  <IconButton
                    aria-label="Menu"
                    aria-haspopup="true"
                    className={classes.menuIcon}
                    onClick={toggleDrawer(anchor, true)}>
                    <MenuIcon />
                  </IconButton>

                  <SwipeableDrawer
                    anchor={anchor}
                    disableSwipeToOpen={false}
                    open={state[anchor]}
                    onClose={toggleDrawer(anchor, false)}
                    onOpen={toggleDrawer(anchor, true)}>
                    {list(anchor)}
                  </SwipeableDrawer>
                </React.Fragment>
              ))}
            </div>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}
